#nav-container {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 100vh;
  width: 100%;
  pointer-events: none;
  z-index: 10;
}

#nav-container:focus-within .bg {
  visibility: visible;
  opacity: 0.6;
}

#nav-container * {
  visibility: visible;
}

.button {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 1;
  -webkit-appearance: none;
  border: 0;
  border-radius: 0;
  height: 70px;
  width: 30px;
  cursor: pointer;
  pointer-events: auto;
  margin-left: 25px;
  touch-action: manipulation;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.icon-bar {
  display: block;
  width: 100%;
  height: 3px;
  background: #409A89;
  transition: 0.3s;
}

.icon-bar + .icon-bar {
  margin-top: 5px;
}

#nav-container:focus-within .button {
  pointer-events: none;
}

#nav-container:focus-within .icon-bar:nth-of-type(1) {
  transform: translate3d(0, 8px, 0) rotate(45deg);
}

#nav-container:focus-within .icon-bar:nth-of-type(2) {
  opacity: 0;
}

#nav-container:focus-within .icon-bar:nth-of-type(3) {
  transform: translate3d(0, -8px, 0) rotate(-45deg);
}

#nav-content {
  margin-top: 70px;
  padding: 20px;
  width: 90%;
  max-width: 300px;
  position: absolute;
  top: 0;
  left: 0;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  overflow-y: scroll;
  max-height: calc(100% - 70px);
  background: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  pointer-events: auto;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  transform: translateX(-101%);
  transition: transform 0.3s;
  will-change: transform;
  contain: paint;
  /* Hide scrollbar for IE, Edge add Firefox */
}
#nav-content::-webkit-scrollbar {
  display: none;
}
#nav-content {
  -ms-overflow-style: none;
  scrollbar-width: none;
  /* Firefox */
}

#nav-content ul {
  height: 100%;
  display: flex;
  flex-direction: column;
  list-style: none;
}

#nav-content li a {
  font-family: "SuisseInt";
  padding: 10px 5px;
  display: block;
  transition: color 0.1s;
  text-decoration: none;
  color: #409A89;
}

#nav-content li a:hover {
  color: #040504;
}

#nav-container:focus-within #nav-content {
  transform: none;
}

.menu {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  overflow: hidden;
  transition-duration: 1s;
  transition-property: height;
  transition-timing-function: ease-in-out;
}
.menu a {
  text-decoration: none;
  color: inherit;
}
.menu-list {
  list-style: none;
  display: flex;
  gap: 25px;
}
.menu-list .menulink {
  margin-left: 40px;
  display: flex !important;
  align-items: flex-start !important;
  font-size: 18px;
  margin-block: 2px;
  text-decoration: none;
  position: relative;
  padding: 0 0 0 5px;
}
.menu-list .menulink img {
  width: auto;
  height: 20px;
}
.menu-list .menulink p {
  margin-left: 5px;
}
.menu-list .menulink::after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #040504;
  border-radius: 4px;
  position: absolute;
  left: 0;
  bottom: 0;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.25s ease-out;
}
.menu-list .menulink:hover::after {
  transform: scaleX(1);
}

